<script>
import MorePage from '@/components/MorePage/index.vue'
export default {
  name: 'myOrder',
  components: {
    MorePage
  },
  data() {
    return {
      // 
      page: 1,
      // 
      limit: 3,
      // 
      myorder: {}
    }
  },

  mounted() {
    // 
    this.getData()
  },

  methods: {
    // 
    async getData() {
      const { page, limit } = this
      let result = await this.$API.reqGetMyOrder(page, limit)
      if (result.code == 200) {
        // 
        this.myorder = result.data
      }
    },
    // 
    getPageNo(page) {
      // 
      this.page = page
      this.getData()
    },
  }
}
</script>

