// 
import { reqSearchList, reqGetDetail } from '@/api/index'

const state = {
  searchList: {},
  goodDetail: {}
}

const mutations = {
  // 
  GETSEARCHLIST(state, searchList) {
    return state.searchList = searchList
  },

  // 
  GETDETAIL(state, goodDetail) {
    return state.goodDetail = goodDetail
  }
}

const actions = {
  // 
  async getSearchList({ commit }, params) {
    let result = await reqSearchList(params)
    if (result.code == 200) {
      commit('GETSEARCHLIST', result.data)
      return 'ok'
    } else {
      return Promise.reject(new Error('faile'))
    }

  },
  // 
  async getDetail({ commit }, skuId) {
    let result = await reqGetDetail(skuId)
    if (result.code == 200) {
      commit('GETDETAIL', result.data)
      return 'ok'
    } else {
      return Promise.reject(new Error('faile'))
    }

  }
}

const getters = {
  // 
  attrsList(state) {
    return state.searchList.attrsList || []
  },
  // 
  goodsList(state) {
    return state.searchList.goodsList || []
  },
  // 
  trademarkList(state) {
    return state.searchList.trademarkList || []
  },

  // 
  categoryView(state) {
    return state.goodDetail.categoryView || []
  },
  // 
  skuInfo(state) {
    return state.goodDetail.skuInfo || []
  },

  // 
  spuSaleAttrList(state) {
    return state.goodDetail.spuSaleAttrList || []
  }

}

export default {
  state,
  mutations,
  actions,
  getters
}