// 
import { reqGetTradeInfo, reqGetUserAddress, reqGetAddress, reqPostOrderInfo } from '@/api/index'


const state = {
  tradeInfo: {},
  userAddress: [],
  address: []
}

const mutations = {
  // 
  GETTRADEINFO(state, tradeInfo) {
    state.tradeInfo = tradeInfo
  },
  // 
  GETUSERADDRESS(state, userAddress) {
    state.userAddress = userAddress
  },
  // 
  GETADDRESS(state, address) {
    state.address = address
  },
  // 

}

const actions = {
  // 
  async getTradeInfo({ commit }) {
    let result = await reqGetTradeInfo()
    if (result.code == 200) {
      commit('GETTRADEINFO', result.data)
      return 'ok'
    } else {
      return Promise.reject(new Error('faile'))
    }
  },

  // 
  getUserAddress({ commit }) {
    let result = reqGetUserAddress()
    if (result.code == 200) {
      commit('GETUSERADDRESS', result.data)
    }
  },
  // Mock
  async getAddress({ commit }) {
    let result = await reqGetAddress()
    if (result.code == 200) {
      commit('GETADDRESS', result.data)
      return 'ok'
    } else {
      return Promise.reject(new Error('faile'))
    }
  },
  // 
  orderInfo(tradeNo, data) {
    let result = reqPostOrderInfo(tradeNo, data)
    console.log(result);
  }
}

const getters = {
  // 
  detailArrayList(state) {
    return state.tradeInfo.detailArrayList
  },
  // 
  orderDetailVoList(state) {
    return state.tradeInfo.orderDetailVoList
  },
  // 
  totalAmount(state) {
    return state.tradeInfo.totalAmount
  },
  // 
  totalNum(state) {
    return state.tradeInfo.totalNum
  },
  // 
  tradeNo(state) {
    return state.tradeInfo.tradeNo
  },
  // 
  address(state) {
    return state.address || []
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}