import axios from "axios";

// mock
const Mock = axios.create({
  baseURL: '/mock',
  timeout: 5000
})

// 
Mock.interceptors.request.use((config) => {
  return config
})

// 
Mock.interceptors.response.use((res) => {
  // 
  // 
  return res.data
}, (error) => {
  // 
  console.log(error);
})

// 
export default Mock



