<template>
  <!--  -->
  <div class="type-nav">
    <div class="container">
      <div @mouseenter="enter" @mouseleave="leave">
        <h2 class="all"></h2>
        <!--  -->
        <div class="sort" @click="goSearch" v-if="show">
          <div class="all-sort-list2">
            <div class="item bo" v-for="l1 in List" :key="l1.categoryId">
              <!--  -->
              <h3>
                <a :data-name="l1.categoryName" :data-l1id="l1.categoryId">{{ l1.categoryName }}</a>
              </h3>
              <div class="item-list clearfix">
                <div class="subitem" v-for="l2 in l1.categoryChild" :key="l2.categoryId">
                  <dl class="fore">
                    <!--  -->
                    <dt>
                      <a :data-name="l2.categoryName" :data-l2id="l2.categoryId">{{ l2.categoryName }}</a>
                    </dt>
                    <dd>
                      <!--  -->
                      <em v-for="l3 in l2.categoryChild" :key="l3.categoryId">
                        <a :data-name="l3.categoryName" :data-l3id="l3.categoryId">{{ l3.categoryName }}</a>
                      </em>

                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <nav class="nav">
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
      </nav>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'GoodsIndex',

  data() {
    return {
      show: true,
    };
  },

  mounted() {
    // 
    if (this.$route.path !== '/home') {
      this.show = false
    }
    // actions
    this.$store.dispatch('getTayNavList')
  },

  computed: {
    // 
    ...mapState({
      List: (state) => state.goods.goodsList
    })
  },

  methods: {
    // queryparams
    goSearch(event) {
      // DOMtarget
      let element = event.target
      // ID
      // ID
      const { name, l1id, l2id, l3id } = element.dataset
      // 
      if (name) {
        // location
        let location = { name: 'search' }
        let query = { name }
        // IDquery
        if (l1id) {
          query.category1Id = l1id
        } else if (l2id) {
          query.category2Id = l2id
        } else if (l3id) {
          query.category3Id = l3id
        }
        // params()
        if (this.$route.params) {
          location.params = this.$route.params
          location.query = query
          this.$router.push(location)
        }
      }
    },

    // 
    leave() {
      // path
      if (this.$route.path !== '/home') {
        this.show = false
      }
    },

    // 
    enter() {
      this.show = true
    }
  },

  watch: {
    // routepathshowtrue
    $route: function () {
      if (this.$route.path == '/home') {
        this.show = true
      }
    }
  }
};
</script>

<style lang="less" scoped>
.type-nav {
  cursor: pointer;
  border-bottom: 2px solid #e1251b;

  .container {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    position: relative;

    .all {
      width: 210px;
      height: 45px;
      background-color: #e1251b;
      line-height: 45px;
      text-align: center;
      color: #fff;
      font-size: 14px;
      font-weight: bold;
    }

    .nav {
      a {
        height: 45px;
        margin: 0 22px;
        line-height: 45px;
        font-size: 16px;
        color: #333;
      }
    }

    .sort {
      position: absolute;
      left: 0;
      top: 45px;
      width: 210px;
      height: 461px;
      position: absolute;
      background: #fafafa;
      z-index: 999;

      .all-sort-list2 {
        .item {
          h3 {
            line-height: 30px;
            font-size: 14px;
            font-weight: 400;
            overflow: hidden;
            padding: 0 20px;
            margin: 0;

            a {
              color: #333;
            }
          }

          .item-list {
            display: none;
            position: absolute;
            width: 734px;
            min-height: 460px;
            background: #f7f7f7;
            left: 210px;
            border: 1px solid #ddd;
            top: 0;
            z-index: 9999 !important;

            .subitem {
              float: left;
              width: 650px;
              padding: 0 4px 0 8px;

              dl {
                border-top: 1px solid #eee;
                padding: 6px 0;
                overflow: hidden;
                zoom: 1;

                &.fore {
                  border-top: 0;
                }

                dt {
                  float: left;
                  width: 54px;
                  line-height: 22px;
                  text-align: right;
                  padding: 3px 6px 0 0;
                  font-weight: 700;
                }

                dd {
                  float: left;
                  width: 415px;
                  padding: 3px 0 0;
                  overflow: hidden;

                  em {
                    float: left;
                    height: 14px;
                    line-height: 14px;
                    padding: 0 8px;
                    margin-top: 5px;
                    border-left: 1px solid #ccc;
                  }
                }
              }
            }
          }

          &:hover {
            background-color: skyblue;

            .item-list {
              display: block;

            }
          }
        }
      }
    }
  }
}
</style>