import { reqGetTayNavList } from '@/api/index'

// 
const state = {
  // 
  goodsList: {}
}

const mutations = {
  // state
  GETTAYNAVLIST(state, goodsList) {
    state.goodsList = goodsList
  }
}

const actions = {
  // 
  async getTayNavList({ commit }) {
    let result = await reqGetTayNavList()
    if (result.code == 200) {
      // mutations
      commit('GETTAYNAVLIST', result.data)
      return 'ok'
    } else {
      // 
      return Promise.reject(new Error('faile'))
    }

  }
}

const getters = {}

export default {
  state,
  mutations,
  actions,
  getters
}