// 
import { reqGetCode, reqRegisterUser, reqLoginUser } from '@/api/index'
import { getToken, getId, getName } from '@/util/token'

// 
const state = {
  code: '',
  userInfo: {
    name: getName(),
    token: getToken(),
    userId: getId()
  }
}

// state
const mutations = {
  // 
  GETCODE(state, code) {
    state.code = code
  },
  LOGINUSERINFO(state, userInfo) {
    state.userInfo = userInfo
  },
  // 
  REMOVEINFO(state) {
    state.userInfo = {}
  }
}

// mutations
const actions = {
  //  
  // commit:mutationsphone:
  async getCode({ commit }, phone) {
    let result = await reqGetCode(phone)
    console.log(result);
    // code=200
    // mutationscode
    if (result.code == 200) {
      commit('GETCODE', result.data)
    }
  },

  // 
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  async registerUserInfo({ commit }, data) {
    let result = await reqRegisterUser(data)
    // 200
    if (result.code == 200) {
      return 'ok'
    } else {
      return Promise.reject(new Error('faile'))
    }
  },

  // 
  async loginUserInfo({ commit }, data) {
    let result = await reqLoginUser(data)
    if (result.code == 200) {
      localStorage.setItem('TOKEN', result.data.token)
      localStorage.setItem('Name', result.data.name)
      localStorage.setItem('USERID', result.data.userId)
      commit('LOGINUSERINFO', result.data)
      return 'ok'
    } else {
      return Promise.reject(new Error('faile'))
    }

  },
  // 
  removeInfo({ commit }) {
    commit('REMOVEINFO')
  }
}

// state
const getters = {}

// 
export default {
  state,
  mutations,
  actions,
  getters
}