<template>
  <div class="register-container">
    <!--  -->
    <div class="register">
      <h3>
        <span class="go"> <router-link to="/login"></router-link></span>
      </h3>
      <div class="content">
        <label>:</label>
        <input v-model="phone" type="text" placeholder="">
        <!-- <span class="error-msg"></span> -->
      </div>
      <div class="content">
        <label>:</label>
        <input v-model="code" type="text" placeholder="">
        <button @click="getCode"></button>
        <!-- <span class="error-msg"></span> -->
      </div>
      <div class="content">
        <label>:</label>
        <input v-model="password" type="text" placeholder="">
        <!-- <span class="error-msg"></span> -->
      </div>
      <div class="content">
        <label>:</label>
        <input v-model="repassword" type="text" placeholder="">
        <!-- <span class="error-msg"></span> -->
      </div>
      <div class="controls">
        <input checked="isCheck" name="m1" type="checkbox">
        <span></span>
      </div>
      <div class="btn">
        <button @click="register"></button>
      </div>
    </div>

    <!--  -->
    <div class="copyright">
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
      <div class="address">6</div>
      <div class="beian">ICP19006430
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'UpshoppingRegister',

  data() {
    return {
      // 
      // 
      phone: '',
      // 
      code: '',
      // 
      password: '',
      // 
      repassword: '',
      // 
      isCheck: true
    };
  },

  mounted() {

  },

  methods: {
    // 
    async getCode() {
      // tryactionsPromisecode
      try {
        const { phone } = this
        // actions
        await this.$store.dispatch('getCode', phone)
        this.code = this.$store.state.user.code
      } catch (error) {
        console.log('');
      }
    },

    // 
    async register() {
      try {
        const { phone, code, password } = this
        // actions
        await this.$store.dispatch('registerUserInfo', { phone, code, password })
        // 
        this.$router.push('/login')
      } catch (error) {
        console.log(error);
      }

    }
  },
};
</script>

